
  .hotel {
    background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
      url(../images/homepage/details/hotel/bg-iphone.jpg);
  
    @include retina2x {
      background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
        url(../images/homepage/details/hotel/bg-iphone-2x.jpg);
    }
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  
    @media screen and (min-width: $mobile) and (max-width: $tablet) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
        url(../images/homepage/details/hotel/bg-iphone.jpg);
      @include retina2x {
        background-image: linear-gradient(
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/homepage/details/hotel/bg-iphone-2x.jpg);
      }
    }
    @media screen and (min-width: $tablet) and (max-width: $desktop) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
        url(../images/homepage/details/hotel/bg-ipad.jpg);
      @include retina2x {
        background-image: linear-gradient(
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/homepage/details/hotel/bg-ipad-2x.jpg);
      }
    }
    @include desktop {
      background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
        url(../images/homepage/details/hotel/bg-des.jpg);
        max-width: $desktop;
        margin: 0 auto;
      @include retina2x {
        background-image: linear-gradient(
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/homepage/details/hotel/bg-des-2x.jpg);
      }
    }
  }
  
