  .hotel__article {
    @media screen and (min-width: 1024px) {
        display: flex;
        justify-content: end;
        
    }
  }

  .hotel__wrapper {
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 1023px) {
        width: 243px;
        justify-content: end;

    }

    @media screen and (min-width: 1024px) {
        width: 620px;
    }
  }

  .about__hotel_logotxt {

    padding-top: 24px;
    margin-bottom: 12px;

    @media screen and (max-width: 413px){
        font-size: 10vw;
    }

    text-align: center;
    @media screen and (max-width: 1023px) {
        text-align: end;
        margin-right: -20px;

    }

    @media screen and (min-width: 1024px) {
        text-align: end;

        padding-top: 48px;
        margin-right: -20px;
        margin-bottom: 16px;

    }

    @media screen and (min-width: 1280px) {
    @include font (64px, 69px, 700, 0.3em); 

        text-align: end;
        margin-right: -20px;
        margin-bottom: 16px;

    }
  }

  .about__hotel_header {
    font-family: Montserrat;
    text-align: center;


    @include font (20px, 24px, 700, 0.02em); 
    color: map-get($colors, 'white-text');

    @media screen and (max-width: 413px){
        font-size: 5vw;
    }

    @media screen and (max-width: 1023px) {
        text-align: end;
        margin-right: -7px;
        margin-bottom: 20px;

    }

    @media screen and (min-width: 1024px) {
        text-align: end;
        margin-right: 0px;
        margin-bottom: 8px;

    }

    @media screen and (min-width: 1280px) {
        @include font (24px, 29px, 700, 0.02em); 
        margin-bottom: 8px;
        text-align: end;
    }
  }

  .about__hotel_subtxt {
    margin-left: 12px;

    @include font (12px, 15px, 600, 0.02em); 
    color: map-get($colors, 'secondary-text');
    font-family: Montserrat;

    @media screen and (max-width: 413px){
        font-size: 3vw;
    }

    @media screen and (max-width: 1023px) {
        width: 243px;
        margin-bottom: 48px;
    }

    @media screen and (min-width: 1024px) {
        @include font (16px, 20px, 600, 0.02em); 
        margin-left: 77px;
        width: 549px;    
    }

  }



.hotel__link {
    text-align: center;
    width: 236px;
}
  